.time-container {
	position: relative;
	width: 100%;
	display: grid;
	grid-template: auto / repeat(5, 1fr);
	gap: 24px;
	margin: 0;
}

@media (max-width: 991px) {
	.time-container {
		grid-template: auto / repeat(4, 1fr);
	}
}

@media (max-width: 575px) {
	.time-container {
		grid-template: auto / repeat(2, 1fr);
	}
}